<template>
  <div class="am- ">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" success-text="刷新成功">

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="list">

    <div v-for="(item, index) in list" :key="index" @click="clickMe($event,index)" class="for">
      <!-- <HeadPor :ref="`child${index}`"
        class="folAndFans"
       :msgg="{id:item.user_id, authen:item.user_authen, img:item.user_img,name:item.user_name,user_lv:item.user_lv,gold:item.user_gold}">
       </HeadPor> -->
       <UserTag :msgg="item" :toUserInfo = "true" class="user_tag" :ref="`child${index}`"></UserTag>
    </div>

  </van-list>

  </van-pull-refresh>
  </div>
</template>

<script>
	/* 引入头像组件*/
	// import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"

  // import {createNamespacedHelpers} from 'vuex'
  // const navBar_Every=createNamespacedHelpers('navBar_Every')
  export default{
    name:"",
    activated(){
      if(this.firstEnter >1 && (this.fromPath != '/no'
      && this.fromPath != '/search')){
        /* 如果不是在第一次进入并且...*/
        // this.mountedFn()

         /* 下面针对含有list的组件*/
        // 缓冲显示
        this.isBuffering = true
        // 清空list
        // this.list = []
        this.list.length = 0
        this.onLoad()
      }else{
        this.firstEnter ++
      }
    },
    /* 数据*/
    data(){
      return {
        firstEnter : 0,
        list : [],
        loading: false,
        isLoading : false,
        finished: false,
        listApi : ['folAndFans','folAndFansMore']
      }
    },
    /* 方法*/
    methods:{
      mountedFn(){
              this.firstEnter ++
              // 缓冲显示
              this.isBuffering = true
            },
      clickMe(e,index){
        this.$refs[`child${index}`][0].toOtherUser(e)
        // console.log(this.$refs[`child${index}`][0])
      },
      toOtherUser(params){
        console.log(params,'params')
          this.$router.push({
            path: '/otherUser',
            query : {
              userId : params
            }
          })
      },
      getList(params,num){
        // this.$api.user[`${this.listApi[num]}`](params).then(
        this.$api.user.folAndFans(params).then(
            res =>{
              //缓冲消失
              this.isBuffering = false
              // 加载状态结束
              this.loading = false;
              // 刷新状态结束
              this.isLoading = false;
              // 允许加载
              this.finished = false;
              console.log(res.data,'关注')
              if(res.data =="no_list_login"){
                //文章触发加载
                this.finished = true
                this.$dialog.confirm({
                          title: '您尚未登录',
                          message: '需要跳转登录页吗?'
                        }).then(() => {
                          // on 点击确定 去登录页
                          this.$router.push({
                            path : '/login'
                          })
                        }).catch(() => {
                          // on cancel
                        });

              }else
              if(res.data =="no_data" ){
                //文章不再触发加载
                this.finished = true
              }else if(res.data.finished){
                // alert(1)
                //如果后端告知无需刷新
                this.finished = true
                this.getFn(res.data.result,num)
              }else if(res.data.ok){
                this.getFn(res.data.result,num)
              }else{
                this.finished = true
              }
                // switch(res.data){
                //   case "no_data":
                //         //文章不再触发加载
                //         this.finished = true
                //    break;
                //   default:
                //   /* 数字*/
                //    switch(num){
                //     case 0:
                //         this.list = res.data
                //         break;
                //     case 1:
                //         this.list = this.list.concat(res.data)
                //         break;
                //       default:
                //    }
                //    //default结束
                // }
            }
        )
      },
      getFn(arg,num){
           switch(num){
            case 0:
                this.list = arg
                break;
            case 1:
                this.list = this.list.concat(arg)
                break;
              default:
           }
      },
      // ...nav.mapMutations({
      //       	signQueryHandler: 'signQueryHandler'
      //       }),
      // 加载
        onLoad() {
            var params = {}
            switch(this.$route.query.folAndFans){
              case 'follow':
                  params = {
                    folAndFans : 'follow',
                  }
                  break;
              case 'fans':
                  params = {
                    folAndFans : 'fans'
                  }
                  break;
            }
            if(this.$route.query.otherUserId){
              //如果query有otherUserId参数 说明是otherUser页面
              params.otherUserId = this.$route.query.otherUserId
            }
            if(!!this.list[0] == true){
              params.time = this.list[this.list.length-1].follow_time
              // if(this.$route.query.otherUserId){
              //   //如果query有otherUserId参数 说明是otherUser页面
              //   params.otherUserId = this.$route.query.otherUserId
              // }
               this.getList(params,1)
            }else{
              // if(this.$route.query.otherUserId){
              //   //如果query有otherUserId参数 说明是otherUser页面
              //   params.otherUserId = this.$route.query.otherUserId
              // }
              this.getList(params,0)
            }

      },
      onRefresh(){
        // this.getList({},0)
        this.list.length = 0
        this.onLoad()
      },
    },
    /* 计算*/
    computed:{
      fromPath:{
        get () {
              return this.$store._modulesNamespaceMap['path/'].context.state.fromPath
        },
        set (value) {
          this.$store._modulesNamespaceMap['path/'].context.commit('fromPathHandler', value)
        }
      },
      isBuffering:{
        get () {
              return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
        },
        set (value) {
          this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
        }
      },
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // HeadPor
      UserTag
    },
    /* 挂载阶段*/
    mounted() {
      this.mountedFn()
        // var params = {}
        // switch(this.$route.query.folAndFans){
        //   case 'follow':
        //       params = {
        //         folAndFans : 'follow'
        //       }
        //       break;
        //   case 'fans':
        //       params = {
        //         folAndFans : 'fans'
        //       }
        //       break;
        // }
        // this.$api.user.folAndFans(params).then(
        //     res =>{
        //       console.log(res.data,'关注')
        //       this.list = res.data
        //         switch(res.data){
        //           case 'repeat_check_in':

        //               break;
        //           default:

        //            //default结束
        //         }
        //     }
        // )

    },
    // 监听
    watch:{
      // 'obj.a': {
      //     handler(newName, oldName) {
      //       console.log('obj.a changed');
      //     },
      //     immediate: true,
      //     // deep: true
      //   }
    }
  }
</script>

<style scoped lang="scss">
  .list{
    // padding: 0 25px;
    .for{
      border-bottom: 1px solid $jsBgcGray;
      background-color : white;
      padding: 0 25px;
      margin-bottom: 20px;
      .user_tag{
      }
    }

  }
</style>



